.post {
    max-width: 450px;

}

.postMessage {
    background-color: white;
    border-radius: 10px;
    margin: 3px;
    padding: 3px;
    display: flex;
    align-items: center;
}

.post img {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 2px solid #f1f5f8;
}


